@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,700&display=swap);
body {
  margin: 0;
  /* font-family: 'Quicksand', sans-serif; */
  font-family: 'Source Sans Pro', sans-serif;

}

nav {
    background: #fff;
    height: 60px;
    display: flex;
    align-items: center;
    padding: 10px;
}
.nav-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo-nav {
    width: 120px;
}

.nav-links a {
    font-size: 14px;
    margin: 0 30px;
    display: inline-block;
    padding: 10px 10px;
    position: relative;
}

.nav-links a:hover {
    color: #9FA8DA
}

.nav-links a:after {    
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    background: #9FA8DA;
    -webkit-transition: width 0.3s ease 0s, left 0.3s ease 0s;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
}

.nav-links a:hover:after { 
    width: 100%; 
    left: 0; 
}

li img{
    height: 23px;
}

.nav-socials {
    display: flex;
    flex-wrap: wrap;
}
.nav-socials li {
    margin-right: 20px;
}

.nav-ico img :hover{
    color: aqua;
}
  /* @media screen and (max-height: 300px) {
      ul {
          margin-top: 40px;
      }
  } */
  
footer {
    background: #2b2b2b;
    padding: 10px;

}

footer p {
    color: #fff;
    font-size: 12px;
}

.footer-content {
    max-width: 600px;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
}

.nav-socials img{
    fill: #fff;
}
.App {
  text-align: center;
  background: #000000;
  height: 100vh;
}

h1, h2, h3, h4, h5, p {
  font-family: 'Quicksand', sans-serif;
}

p {
  font-weight: 100;
}

a {
  font-weight: 200;
  letter-spacing: 1px;
  color: #000000;
  text-decoration: none;
}

ul {
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}
